.dialogs {
  display: grid;
  grid-template-columns: 4fr 8fr;
}
.dialogsWrapper,
.messageWrapper {
  background-color: #fff;
  border: 1px solid #eeeeee;
}
.dialogsWrapper h3 {
  height: 95px;
  margin: 0;
  display: flex;
  align-items: center;
  padding: 0 20px;
}
.messageWrapper {
  border-left: none;
}
.addFormMessage {
  width: 100%;
  background-color: #fff;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
  border-radius: 2px;
}
.addFormMessage textarea {
  width: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  /*height: 40px;*/
  border: 1px solid #eeeeee;
  padding: 5px;
}
.addFormMessage form {
  display: grid;
  grid-template-columns: 80% 20%;
}
.btn {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  border: none;
  background-color: #e44d3a;
  color: #fff;
  font-size:18px;
}
@media screen and (max-width: 700px) {
  .dialogsWrapper h3 {
    padding: 0 5px;
  }
  .addFormMessage form {
    display: block;;
  }
  .btn {
    width: 100%;
  }
}