.formControl.error input,
.formControl.error textarea {
    border: 1px solid red;
}
.formControl {
    position: relative;
 }
.formControl.error span {
   color: red;
}
.formSummaryError {
    border: red 1px solid;
    color: red;
}
.formTextError {
    display: block;
    -ms-text-align: left;
    text-align: left;
    background-color: #fbb3ab;
    padding-left: 5px;
    border-radius: 4px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
    position: absolute;
    top: -21px;
    left: 0;
    width: 100%;
    box-sizing: border-box;
}
