.myPost {
  grid-area: myPost;
}
.addFormPost {
  width: 100%;
  background-color: #fff;
  border-top: 5px solid #e44d3a;
  padding: 10px;
  box-sizing: border-box;
  margin-bottom: 20px;
  border-radius: 2px;
}
.addFormPost textarea {
  width: 100%;
  box-sizing: border-box;
  border-radius: 4px;
  height: 40px;
  border: 1px solid #eeeeee;
  padding: 5px;
}
.addFormPost form {
  display: grid;
  grid-template-columns: 80% 20%;
}
.btn {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  border: none;
  background-color: #e44d3a;
  color: #fff;
  font-size:18px;
  margin-left: 5px;
  width: 100%;
  height: 40px;
  cursor: pointer;
}

