.profileStatus:hover {
    border: 1px solid #e0ebff;
    padding: 0px;
    box-sizing: border-box;
    width: 100%;
    font-size: 14px;
    background-color: #e0ebff;
}

.profileStatusInput {
    height: auto;
    padding: 0 10px;
    padding-bottom: 21px;

}
.profileStatusInput input {
    width: 90%;
    padding: 5px;
    box-sizing: border-box;
    border: 1px solid #b0b0b0;
    font-size: 13px;
    font-weight: normal;
    position: absolute;
    left: 12px;
    
}
