.dialogsItem {
  display: flex;
  padding: 20px;
  border-top: 1px solid #eeeeee;
}
.dialogsItem:hover {
  background-color: #eeeeee;
  cursor: pointer;
}
.dialogsItemActive {
  background-color: #eeeeee;
}
.dialogsItem:last-child {
  border-bottom: 1px solid #eeeeee;
}
.dialogsItem img {
  width: 50px;
  margin-right: 10px;
}
.dialogsItem a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}
@media screen and (max-width: 700px) {
  .dialogsItem {
    display: block;
    padding: 10px;
  }
}