.nav {
  display: flex;
  align-items: center;
}
.item {
  margin-right: 30px;
  color: #fff;
  font-size: 18px;
}
.item:last-child {
  margin-right: 0px;
}
.item a {
  text-decoration: none;
  cursor: pointer;
}
.item a:visited {
  color: #fff;
}
.item a:hover{
  color: #000;
}
.item a.active{
  color: #000;
}
@media screen and (max-width: 400px) {
  .item {
    margin-right: 15px;
  }
}