.wrapperLogin {
    position: absolute;
    top: 38px;
    left: 0;
    background-image: url(../../assets/bg-image.jpg);
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    background-size: cover;
}
.login {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    background-color: #fff;
    text-align: center;
    width: 320px;
    box-sizing: border-box;
}
.login h1 {
    font-size: 26px;
    margin: 0;
    margin-bottom: 20px;
}
.login p {
    margin-bottom: 20px;
    display: block;
}
.formGrup {
    margin-bottom: 20px;
}
.formGrup input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    border: 1px solid #e8e7e7;
    border-radius: 2px;
    color: #717171;
}
.checkbox {
    display: flex;
    margin-bottom: 20px;
}
.btn {
    width: 100%;
    background-color: #e44d3a;
    border: none;
    color: #fff;
    cursor: pointer;
    padding: 10px;
    font-size: 18px;
    border-radius: 2px;
}
.btn:hover {
    background-color: #c14738;
}