.profile {
  text-align: center;
  background-color: #fff;
  grid-area: profile;
  height: 100%;
  font-size: 16px;
  font-weight: normal;
  position: relative;
}

.userPic {
  background-color: #e44d3a;
  position: relative;
  height: 100px;
}
.img {
  position: relative;
  top: 100%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  background-color: #fff;
}
.img img {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  width: 90%;
}
.name {
  padding-top: 60px;
  padding-bottom: 30px;
  padding-left: 10px;
  padding-right: 10px;
}

.wrapper {
  display: grid;
  grid-template-areas: "profile myPost";
  grid-template-columns: 3fr 9fr;
  grid-gap: 30px;
}
.border {
  border: 1px solid #eeeeee;
}
.following,
.followers {
  padding: 20px 0;
}
.following h4,
.followers h4 {
  margin: 0;
  margin-bottom: 10px;
  color: #a5a5a5;
}
.following span,
.followers span {
  color: #3b3b3b;
}

@media screen and (max-width: 900px) {
  .wrapper {
    display: block;
  }
  .userPic {
    height: 50px;
  }
  .name {
    padding-top: 45px;
  }
  .name h3 {
    margin: 0;
  }
  .following,
  .followers {
    padding: 5px 0;
  }
}
